import React from "react";

class WizardSteps extends React.Component {
  constructor(props) {
    super(props);
    this.handlePreviousStep = this.handlePreviousStep.bind(this);
    this.handleNextStep = this.handleNextStep.bind(this);
    this.handleTransport = this.handleSearch.bind(this);
    this.returnTask = this.returnTask.bind(this);
    this.state = { step: 0, prevBtnDisabled: false, nextBtnDisabled: false };
  }

  componentDidMount() {
    if (this.state.step === 0) {
      this.setState({ prevBtnDisabled: true });
    }
  }

  handlePreviousStep(e) {
    e.preventDefault();
    let currentStep = this.state.step;
    console.log(currentStep);

    if (currentStep !== 0) {
      this.setState({ nextBtnDisabled: false });
      var optionBox = document.getElementsByClassName("options-box");
      optionBox[0].children[currentStep].style.display = "none";
      optionBox[0].children[currentStep - 1].style.display = "block";
      this.setState({ step: currentStep - 1 });
      if (currentStep - 1 === 0) {
        this.setState({ prevBtnDisabled: true });
      }
    } else {
      console.log("Can't go back");
    }
  }

  handleNextStep(e) {
    e.preventDefault();
    let currentStep = this.state.step;

    if (currentStep !== 4) {
      this.setState({ prevBtnDisabled: false });
      var optionBox = document.getElementsByClassName("options-box");
      optionBox[0].children[currentStep].style.display = "none";
      optionBox[0].children[currentStep + 1].style.display = "block";
      this.setState({ step: currentStep + 1 });
      if (currentStep + 1 === 4) {
        this.setState({ nextBtnDisabled: true });
      }
    } else {
      console.log("Can't go forth");
    }
  }

  handleSearch(e) {
    e.preventDefault();
  }

  returnTask() {
    switch (this.state.step) {
      case 0:
        return "Zvolte zkladn hodnoty pro vyhledn Vaeho spoje";
      case 1:
        return "Vyberte si parametry Va trasy";
      case 2:
        return "Vyberte Vmi preferovan zpsob dopravy";
      case 3:
        return "V ppad dalch poadavk na spoj zvolte ne";
      case 4:
        return "Po kontrole Vaeho zadn vyhledejte odpovdajc spoj";
      default:
        return "Chyba v programu";
    }
  }

  render() {
    return (
      <section className="wizardSteps">
        <div className="wizardTasks">
          <label>
            <b>Krok {this.state.step + 1} z 5</b>
          </label>
          <label>{this.returnTask()}</label>
        </div>
        <table>
          <tbody>
            <tr>
              <td>
                <button
                  onClick={this.handlePreviousStep}
                  id="prev"
                  disabled={this.state.prevBtnDisabled}
                >
                  Pedchoz krok
                </button>
              </td>
              <td>
                <button
                  onClick={this.handleNextStep}
                  id="next"
                  disabled={this.state.nextBtnDisabled}
                >
                  Dal krok
                </button>
              </td>
            </tr>
          </tbody>
        </table>
      </section>
    );
  }
}

export default WizardSteps;
